const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			formNode: {
				real_name: '',  //真是姓名
				id_card: '',   //身份证
				phone: '',
				card_positive: '',  //身份证正面
				card_peverse: '',
			},
			imgUrl: ''
		}
	},
	onLoad() {
		this.imgUrl = helper.imgUrl
	},
	methods: {
		inputValue(e){
			let formNode = this.formNode
			let name = e.currentTarget.dataset.name
			let value = e.detail.value
			formNode[name] = value
			this.formNode = formNode
			console.log(this.formNode)
		},
		getCard(e){
			console.log(e)
			let type = e
			this.getImg(type)
		},
		//上传图片
		getImg(op){
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'],
				success: res => {
					uni.showToast({
						title: '图片上传中',
						icon: 'loading'
					});
					Promise.all(
						res.tempFiles.map(item => {
							return new Promise((resolve, reject) => {
								uni.uploadFile({
									url: `${helper.requestUrl}/posts/uploads`,
									filePath: item.path,
									name: 'file',
									header: {
										authorization: app.globalData.token
									},
									success: res => {
										console.log(res)
										resolve({
											path: JSON.parse(res.data).data
										});
									}
								});
							});
						})
					).then(e => {
						uni.hideToast();
						console.log(e[0].path.path,'++++++++++')
						if(op == 1){
							this.formNode.card_positive = e[0].path.path
							console.log(this.formNode.card_positive)
						}else{
							this.formNode.card_peverse = e[0].path.path
						}
					}).catch(err => console.log(err));
				}
			});
		},
		submint(){
			if (this.formNode.real_name == '') {
				wx.showToast({
					title: "请填写您的真实姓名",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if (this.formNode.id_card == '') {
				wx.showToast({
					title: "请输入18位有效身份证号",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if (this.formNode.id_card.length != 18) {
				wx.showToast({
					title: "请输入18位有效身份证号",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if (this.formNode.phone == '') {
				wx.showToast({
					title: "请输入您的联系方式",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if (this.formNode.phone.length != 11) {
				wx.showToast({
					title: "请输入您的联系方式",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if (this.formNode.card_positive == '') {
				wx.showToast({
					title: "请上传身份证正面",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if (this.formNode.card_peverse == '') {
				wx.showToast({
					title: "请上传身份证反面",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			uni.showLoading({
			  title: '认证中...',
				duration: 1000
			});
			uni.request({
				url: `${helper.requestUrl}/user/real-check`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: this.formNode,
				success: res => {
					console.log(res);
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res,'*-*****')
					if (res.data.status_code == 200) {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
						uni.reLaunch({
							url:'/pages/me'
						})
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
}
